.person-list{
    max-width: 700px;
    margin: 20px auto 0;
    padding: 0 20px;
}

.person-list .actions{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.person-list .actions button{
    padding: 10px;
    border-radius: 5px;
    border: 0;
    background: #da552d;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}


.person-list .actions button:disabled{
    opacity: 0.5;
    cursor: default;
    
 }

.person-list .actions button:hover:not([desabled]){
   opacity: 0.7;
   
}